body {
  padding: 5px;
  background-color: #333;
  font-family: sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.containerBox {
  display: block;
  justify-content: center
  
}


h2 {
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 30pt;
  display: flex;
  justify-content: center;
  color: yellow;
}


li {
  color: #333;
  background-color: rgba(255, 255, 255, 5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  list-style: none;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

  transition: background-color .2s ease-out;
}

li:hover {
  background-color: pink;
  cursor: pointer;
}

ul {
  padding: 0;
}

input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 165;
}

button {
  padding: 10px;
  font-size: 16px;
  margin-right: 0px;
  background-color: #0066ff;
  color: #FFF;
  border: 2px solid #0066ff;
}

.containerBox button {
  margin-left: 50px;
  align-items: stretch;
}

.cat {
  display: flex;
  justify-content: center;
}


h1 {
  padding: 5px;
  font-style: italic;
  font-family: fantasy;
  font-size: 35px;
  
}